.main {
    width: 100%;
    // min-height: 90vh;
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wrapper {
    margin: 0 auto;
    height: fit-content;
    width: 90vw;
    max-width: 1000px;
    min-width: 300px;
    border: 1px solid white;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;

}

.createNoteWrapper {
    position: relative;
    display: flex;
    gap: 20px;

    &__note {
        padding: 10px;
        border-radius: 5px;
        border: 2px solid rgba(0, 0, 0, 0.244);
        box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.397);
        margin: 0 auto;
        /* width: 300px; */
        width: 310px;

        >div:first-child {
            display: flex;
            align-items: center;

            >div {
                margin: 0 auto;
                height: 40px;

                input: {
                    height: 100%;
                }
            }
        }
    }
}